      </button>
      <button class="notes__item-btn del" @click="$emit('delNote',note.id)">
        <img src="@/assets/img/del.svg" alt="" />
        <span>{{ words.deledit[lang] }}</span>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  inject: ["words"],
  props: {
    lang: String,
    grid: Boolean,
    note: Object
  },
};
</script>

